import React from 'react';
import { Link } from 'react-router-dom';
import { FaComments } from "react-icons/fa";

const Chats = () => {
    return (
        <>
            <Link to="" title='Chat'>
                <div style={{zIndex: '9999', background: 'var(--cb-green)', width: '60px', height: '60px', position: 'fixed', top: '89vh', right: '20px',borderRadius: '50%', display: 'flex', alignItems: 'center', justifyContent: 'center', animation: 'fadezoom 3s infinite'}}>
                    <FaComments style={{color: 'var(--cb-white)', fontSize: '1.5rem'}} />
                </div>
                <style>{` @keyframes fadezoom { 0% {transform: scale(1);} 50% {transform: scale(1.1)} 100% {transform: scale(1);} } `}</style>
            </Link>  
        </>
    );
};

export default Chats;